li.list-item {
  padding: 1rem 0.5rem;
  display: flex;
  width: 50%; }
  li.list-item > a {
    display: flex;
    width: 100%; }
  @media (min-width: 1024px) {
    li.list-item {
      width: 25%; } }
  li.list-item article {
    border-radius: 0.5rem;
    overflow: hidden;
    background-color: #666;
    width: 100%; }
  li.list-item .img {
    position: relative; }
    li.list-item .img img {
      width: 100%; }
  li.list-item .profile {
    padding: 1rem; }
